import { Component, OnInit, Input, HostListener } from '@angular/core';
import { TmdbService } from 'src/app/services/tmdb.service';
import { FilmDetail, SerieDetail, Credits, Cast, Crew, JustWatchPostBody, JustWatchResponse } from 'src/app/interfaces/interfaces';
import { ModalController } from '@ionic/angular';
import { JustwatchService } from 'src/app/services/justwatch.service';

const screenWidthBreakPoint = 1234;
const screenWidthBreakPointMid = 720;

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss'],
})
export class DetailComponent implements OnInit {

  screenWidth: number;

  @Input('id') id;
  @Input('name') name;
  @Input('title') title;

  film: FilmDetail;
  serie: SerieDetail;
  actors: Cast[] = [];
  director: Crew;
  oculto: number = 150;
  justWatchPostBody: JustWatchPostBody = {
    country: 'ES',
    search: '',
    idtmdb: this.id,
    type: '',
    tmdb_vote: 0,
    tmdb_popularity: 0
  };
  justWatchProviders: Array <JustWatchResponse> = [];
  rentProviders: Array <JustWatchResponse> = [];
  buyProviders: Array <JustWatchResponse> = [];
  streamingProviders: Array <JustWatchResponse> = [];

  slideOptActores = {};
  slideOptProviders = {};

  constructor(private tmdb: TmdbService,
    private modalCtrl: ModalController,
    private justWatch: JustwatchService) {}

  ngOnInit() {
    this.loadJustWatchPostBody();
    this.slideOptActores = {
      slidesPerView: 3.3,
      centeredSlides: true,
      centeredSlidesBounds: true,
      freeMode: true
    }
    this.slideOptProviders = {
      slidesPerView: 2.3,
      scrollbar: true,
      centeredSlides: true,
      centeredSlidesBounds: true,
      freeMode: true
    }
    this.getScreenSize();
    this.loadDetails();
  }

  loadDetails(): void{
    if(this.title){
      this.tmdb.getMovieDetail(this.id).subscribe( res => {
        this.film = res;
          console.log(res)
      })
      this.tmdb.getMovieCredits(this.id).subscribe( res => {
        this.actors = res.cast;
        let crew = res.crew;
        this.director = crew.filter(element => element.job == "Director")[0];
        console.log(this.director)
      })
    } else if (this.name){
      this.tmdb.getSerieDetail(this.id).subscribe( res => {
        this.serie = res
          console.log(res)
      })
      this.tmdb.getSerieCredits(this.id).subscribe( res => {
        this.actors = res.cast;
      })
    }
  }

  loadJustWatchPostBody(): void {
    this.justWatchPostBody.idtmdb = this.id
    if(this.title){
      this.justWatchPostBody.search = this.title;
      this.justWatchPostBody.type = "movie";
    }else if(this.name){
        this.justWatchPostBody.search = this.name;
        this.justWatchPostBody.type = "tv";
    }
    console.log("justçwatch", this.justWatchPostBody)
    this.justWatch.getPlatforms(this.justWatchPostBody).subscribe(res => {
      this.justWatchProviders = res;
      this.buyProviders = this.justWatchProviders.filter(e => { return e.monetization_type == "buy"})
      this.rentProviders = this.justWatchProviders.filter(e => { return e.monetization_type == "rent"})
      this.streamingProviders = this.justWatchProviders.filter(e => { return e.monetization_type == "flatrate"})
    })
  }

  @HostListener('window:resize', ['$event'])
  getScreenSize(event?) {
    this.screenWidth = window.innerWidth;
    if (this.screenWidth > screenWidthBreakPoint){
      this.slideOptActores = {
        slidesPerView: 4.5,
        centeredSlides: true,
        centeredSlidesBounds: true,
        freeMode: true
      }
    } else if (this.screenWidth > screenWidthBreakPointMid) {
      this.slideOptActores = {
        slidesPerView: 4.3,
        centeredSlides: true,
        centeredSlidesBounds: true,
        freeMode: true
      }
    } else {
      this.slideOptActores = {
        slidesPerView: 3.3,
        centeredSlides: true,
        centeredSlidesBounds: true,
        freeMode: true
      }
    }
  }

  return(): void{
    this.modalCtrl.dismiss();
  }

}
