import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { JustWatchResponse } from '../interfaces/interfaces';
import { Observable } from 'rxjs';

const URL = "http://127.0.0.1:5000/api/search";

@Injectable({
  providedIn: 'root'
})
export class JustwatchService {
  constructor(private http: HttpClient) { }

  getPlatforms(body): Observable <Array<JustWatchResponse>>{
    return this.http.post<Array<JustWatchResponse>>(URL, body)
  }
}
