import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { TmdbResponse, FilmDetail, SerieDetail, Credits } from '../interfaces/interfaces';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';

const URL = environment.tmdbUrl;
const APIKEY = environment.tmdbApiKey;

@Injectable({
  providedIn: 'root'
})

export class TmdbService {

  private popularFilmPage: number = 0;
  private popularSeriePage: number = 0;

  constructor(private http: HttpClient) { }

  private executeQuery<T>(query: string){
    query = URL + query;
    query += `&api_key=${APIKEY}&language=es&include_image_language=es`;
    return this.http.get<T>(query);
  }

  getMovieDetail(id: string){
    return this.executeQuery<FilmDetail>(`/movie/${id}?a=1`)
  }

  getMovieCredits(id: string){
    return this.executeQuery<Credits>(`/movie/${id}/credits?a=1`)
  }

  getSerieDetail(id: string){
    return this.executeQuery<SerieDetail>(`/tv/${id}?a=1`)
  }

  getSerieCredits(id: string){
    return this.executeQuery<Credits>(`/tv/${id}/credits?a=1`)
  }

  getMoviesOfMonth(){
    const today = new Date();
    const lastDay = new Date(today.getFullYear(), today.getMonth() + 1, 0).getDate();
    let month = today.getMonth() + 1;
    let monthString;
    if (month < 10){
      monthString = '0' + month
    } else {
      monthString = month;
    }
    const initDate = `${today.getFullYear()}-${monthString}-01`;
    const lastDate = `${today.getFullYear()}-${monthString}-${lastDay}`;
    return this.executeQuery<TmdbResponse>(`/discover/movie?primary_release_date.gte=${initDate}&primary_release_date.lte=${lastDate}`)
  }

  getTrendings(){
    const query = '/trending/all/week?';
      return this.executeQuery<TmdbResponse>(query)
  }

  getTrendingSeries(){
    const query = '/trending/tv/week?';
      return this.executeQuery<TmdbResponse>(query)
  }

  getPopularFilms(){
    this.popularFilmPage++;
    const query = `/discover/movie?sort_by=popularity.desc&page=${this.popularFilmPage}`;
      return this.executeQuery<TmdbResponse>(query)
  }

  getPopularSeries(){
    this.popularSeriePage++;
    const query = `/discover/tv?sort_by=popularity.desc&page=${this.popularFilmPage}`;
    return this.executeQuery<TmdbResponse>(query)
  }

  searchFilms(text: string){
    const query = `/search/movie?query=${text}`;
    return this.executeQuery<TmdbResponse>(query)
  }

  searchSerie(text: string){
    const query = `/search/tv?query=${text}`;
    return this.executeQuery<TmdbResponse>(query)
  }

}
